:root {
  --grid-cols: 4;
  --grid-gutters: 1rem;
  --grid-max-width: 57.445rem;
  --grid-baseline: 16px;
}

@media only screen and (min-width: 740px) {
  :root {
    --grid-cols: 12;
  }
}

body {
  font-family: "Lato", "Helvetica Neue", Arial, sans-serif !important;
}

.Tree-title,
.Tree-item,
.Tree-collectionLabel {
  font-family: "Lato", "Helvetica Neue", Arial, sans-serif;
  font-size: 1rem;
}

.Status-dot {
  border-width: 0.25rem;
}

.Header-title:before {
  background-image: url("../img/eq.png");
  background-repeat: no-repeat;
  content: "";
  display: inline-block;
  width: 25px;
  height: 25px;
  background-position: 0;
  background-size: contain;
  vertical-align: bottom;
  margin-right: 0.5rem;
}

.Prose {
  max-width: 100%;
  color: #222;
  font-family: "Lato", "Helvetica Neue", Arial, sans-serif;
  font-variant-numeric: proportional-nums;
  -moz-font-feature-settings: "pnum";
  -webkit-font-feature-settings: "pnum";
  font-feature-settings: "pnum";
}

.Prose p {
  max-width: 44em;
}

.Prose h1,
.Prose h2,
.Prose h3,
.Prose h4,
.Prose h5,
.Prose h6 {
  color: #222;
  font-family: "Lato", "Helvetica Neue", Arial, sans-serif;
}

.Prose tr:nth-child(even) {
  background-color: transparent;
}

.Prose tr {
  border-bottom: 1px solid #eee;
}

.Prose tr:last-of-type {
  border-bottom: none;
}

.Prose img {
  margin: 1rem 0;
  width: 100%;
}

.Pen {
  font-family: "Lato", "Helvetica Neue", Arial, sans-serif;
}

.Pen .Pen-title {
  font-family: "Lato", "Helvetica Neue", Arial, sans-serif;
}

.eq-font-lato {
  font-family: "Lato", "Helvetica Neue", Arial, sans-serif;
  font-size: 1.5rem;
  letter-spacing: 0.05rem;
}

.eq-font-helvneue {
  font-family: "Helvetica Neue";
  font-size: 1.5rem;
  letter-spacing: 0.05rem;
}

.eq-font-helv {
  font-family: "Helvetica";
  font-size: 1.5rem;
  letter-spacing: 0.05rem;
}

.eq-font-arial {
  font-family: "Arial";
  font-size: 1.5rem;
  letter-spacing: 0.05rem;
}

.eq-font-sans-serif {
  font-family: sans-serif;
  font-size: 1.5rem;
  letter-spacing: 0.05rem;
}

.eq-docs-img {
  float: right;
}

.eq-docs-img.float-right {
  float: right;
  margin: 0 0 1rem 1rem;
}

.eq-docs-img.float-left {
  float: left;
  margin: 0 1rem 1rem 0;
}

.eq-docs-img.w50pc {
  width: 50%;
}

h3 {
  display: block;
  width: 100%;
  clear: both;
}

.eq-tile-container {
  display: block;
  position: relative;
  float: left;
  clear: both;
  width: 100%;
  margin-bottom: 1rem;
}

.eq-tile {
  width: 150px;
  height: 215px;
  border-radius: 3px;
  box-shadow: 0 0 1px 0 rgba(0, 0, 0, 0.15), 0 1px 2px 0 rgba(0, 0, 0, 0.1);
  margin-bottom: 1rem;
  margin-right: 1rem;
  box-sizing: content-box;
  float: left;
  font-size: 0.8rem;
  overflow: hidden;
}

.eq-tile:hover {
  box-shadow: 0 0 1px 0 rgba(0, 0, 0, 0.3), 0 1px 2px 0 rgba(0, 0, 0, 0.4);
}

.eq-tile-colour {
  min-height: 40%;
  text-align: center;
}

.eq-tile-panel-text {
  font-size: 24pt;
  font-weight: 900;
  font-family: "Lato";
  padding-top: 1.3rem;
  display: block;
}

.eq-tile-info {
  padding: 0.4rem;
  background-color: #fff;
  box-sizing: border-box;
}

.eq-tile-info-title {
  font-size: 0.8rem !important;
}

.Prose table.eq-tile-info-table {
  margin: 1rem 0;
}

.eq-tile-info-table td {
  padding: 0.3rem;
  font-size: 0.7rem;
  vertical-align: top;
}

.eq-tile-info-table tr td:first-child {
  color: #999;
}

